@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;700;800&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}

html{height:100%}body{position:relative;height:100%;min-width:320px;font-family:Inter, sans-serif;font-size:18px;line-height:1.33;background:#F7F9EF;color:#25425B}#root{height:100%}button,input,textarea{border:none;margin:0;padding:0;background:transparent;color:inherit;font:inherit;line-height:normal}input,textarea{outline:none}button{width:auto;cursor:pointer}

.app{height:100%}

.quiz,.quizQuestion,.quizCheckout{display:-webkit-flex;display:flex;min-height:100%}.quizQuestion{background:#F7F9EF;-webkit-align-items:center;align-items:center}.quizCheckout{background:#F7F9EF}.container{padding:20px 24px;width:100%;max-width:550px;margin:0 auto}

.single-select-question__head{margin-bottom:20px}@media only screen and (min-width: 480px){.single-select-question__head{text-align:center}}.single-select-question__title{font-weight:800;font-size:24px;line-height:28px}.single-select-question__title b{color:#E6957E}.single-select-question__subtitle{margin-top:6px}.single-select-question__subtitle b{font-weight:bold;color:#E6957E}.single-select-question__answer{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;margin:0 auto 8px;padding:16px;background:#91B386;width:100%;min-height:90px;max-width:380px;border-radius:8px;text-align:left}.single-select-question__answer:last-child{margin-bottom:0}.single-select-question__icon{font-size:44px;margin-right:16px}.single-select-question__answer-title{font-weight:600;color:#FFFFFF;font-size:20px}

.input-question__head{margin-bottom:50px}@media only screen and (min-width: 480px){.input-question__head{text-align:center}}.input-question__title{font-weight:800;font-size:24px;line-height:28px}.input-question__title b{color:#E6957E}.input-question__subtitle{margin-top:6px}.input-question__subtitle b{font-weight:bold;color:#E6957E}.input-question__input{display:block;border-style:none none solid;border-width:0 0 1px;border-color:transparent transparent #D5DBC0;background-color:#F7F9EF;height:24px;margin-bottom:45px;font-size:18px;line-height:24px;width:100%;padding:8px 12px;text-align:center}.input-question__btn{display:block;width:100%;max-width:360px;margin:auto;padding:17px;border-radius:12px;background-color:#91B286;color:#FFFFFF;font-size:16px;line-height:1;font-weight:700;text-align:center}.input-question__btn:disabled{opacity:0.5}

.progress-loader{width:100%;text-align:center}

.checkoutImage{display:block;width:257px;height:auto;margin:0 auto 35px}.checkoutButton{display:block;width:100%;max-width:360px;margin:0 auto;padding-top:15px;padding-bottom:13px;border-radius:41px;color:#FFFFFF;background-color:#91B286;text-align:center;letter-spacing:1px;font-weight:bold;font-size:16px;line-height:24px}.checkoutButton b{font-weight:800}.checkoutText{text-align:center;font-size:16px;font-weight:500;margin:35px auto 16px}.checkoutText strong{font-size:24px;font-weight:700}.checkoutText b{color:#E6957E;font-weight:700}.checkoutText em{font-style:italic}.checkoutCards{padding-top:24px;margin-bottom:35px}.checkoutCardsTitle{font-size:24px;line-height:1.2;margin-bottom:45px;font-weight:700;text-align:center}.checkoutCard,.checkoutCardSelected{position:relative;display:grid;grid-template-columns:1fr 144px;min-height:78px;cursor:pointer}.checkoutCard:not(:last-child),.checkoutCardSelected:not(:last-child){margin-bottom:24px}.checkoutCardLabel{position:absolute;left:16px;bottom:calc(100% - 12px);padding:4px 14px;background:#BDBDBD;border-radius:66px;font-weight:700;font-size:14px;letter-spacing:0.03em;color:#FFFFFF}.checkoutCardSelected .checkoutCardLabel{background:#E6957E}.checkoutCardPeriod{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:10px;background:#F7F9EF;border-radius:12px 0 0 12px;border-style:solid;border-width:1px;border-color:#E0E0E0;color:#494949;font-weight:800;font-size:16px;letter-spacing:0.5px}.checkoutCardSelected .checkoutCardPeriod{color:#494949;background:#FFFFFF;border-color:#E6957E;box-shadow:0px 5.69686px 3.41812px rgba(71,103,186,0.15)}.checkoutCardPrice{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:10px;background:#F7F9EF;border-radius:0 12px 12px 0;border-style:solid;border-width:1px 1px 1px 0;border-color:#E0E0E0;color:#494949;font-weight:700;font-size:16px}.checkoutCardSelected .checkoutCardPrice{color:#FFFFFF;background:#E6957E;border-color:#E6957E;box-shadow:0px 5.69686px 3.41812px rgba(71,103,186,0.15)}.moneyBack{position:relative;border:4px solid #91B286;border-radius:10px;padding:17px 20px 40px;background-color:#F7F9EF;margin:35px auto 52px}.moneyBackTitle{margin-bottom:12px;font-weight:700;font-size:24px;line-height:1.2;text-align:center}.moneyBackText{font-size:16px;line-height:20px;font-weight:500}.moneyBackStamp{position:absolute;right:22px;bottom:-52px;fill:#91B286}

.thank-you__title{font-weight:800;font-size:24px;line-height:28px;margin-bottom:20px}.thank-you__title b{color:#E6957E}.thank-you__subtitle{margin-bottom:6px}.thank-you__subtitle b{font-weight:bold;color:#E6957E}

